import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { IGeneralTopicQuestion } from '../shared/model/content/general-topic.question.model';
import { IGeneralTopicContent } from '../shared/model/content/general-topic-content.model';

type EntityResponseType = HttpResponse<IGeneralTopicQuestion>;
type contentEntityResponseType = HttpResponse<IGeneralTopicContent>;
type contentArrayResponseType = HttpResponse<IGeneralTopicContent[]>;

let headers: HttpHeaders = new HttpHeaders();
headers = headers.append('Access-Control-Allow-Origin', '*').append('Access-Control-Allow-Headers', '*');

@Injectable({ providedIn: 'root' })
export class GeneralConceptAndTopicService {

    public generalTopicsResourceUrl = environment.API_URL + 'services/content/api/general-topics/all-details';
    public topicContentResourceUrl = environment.API_URL + 'services/content/api/general-topic-contents';
    public contentResourceUrl = environment.API_URL + 'services/content/api/contents';
    public questionResourceUrl = environment.API_URL + 'services/content/api/questions';

    constructor(protected http: HttpClient) { }

    getTopicContentList(id: number): Observable<contentEntityResponseType> {
        return this.http.get<IGeneralTopicContent>(`${this.generalTopicsResourceUrl}/${id}`, { observe: 'response' });
    }

    getTopicQuestionList(id: number): Observable<EntityResponseType> {
        return this.http.get<IGeneralTopicQuestion>(`${this.generalTopicsResourceUrl}/${id}`, { observe: 'response' });
    }

    getContentByContentId(id: number): Observable<contentEntityResponseType> {
        return this.http.get<IGeneralTopicContent>(`${this.contentResourceUrl}/${id}`, { observe: 'response' });
    }

    updateContent(content: IGeneralTopicContent): Observable<contentEntityResponseType> {
        return this.http.put<IGeneralTopicContent>(this.contentResourceUrl, content, { headers: headers, observe: 'response' });
    }

    saveTopicContent(topicContents: IGeneralTopicContent): Observable<contentEntityResponseType> {
        return this.http.post<IGeneralTopicContent>(this.topicContentResourceUrl, topicContents, { headers: headers, observe: 'response' });
    }

    updateTopicContents(topicContents: IGeneralTopicContent[]): Observable<contentArrayResponseType> {
        return this.http.put<IGeneralTopicContent[]>(this.topicContentResourceUrl, topicContents, { headers: headers, observe: 'response' });
    }

    deleteTopicContents(contentIds): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.topicContentResourceUrl}/${"batch"}/${contentIds}`, { observe: 'response' });
    }

    saveTopicQuestion(question: IGeneralTopicQuestion): Observable<EntityResponseType> {
        return this.http.post<IGeneralTopicQuestion>(this.questionResourceUrl, question, { headers: headers, observe: 'response' });
    }

    deleteTopicQuestions(questionIds): Observable<HttpResponse<any>> {
        return this.http.delete<any>(`${this.questionResourceUrl}/${"batch"}/${questionIds}`, { observe: 'response' });
    }

}
