import { GeneralTopicQuestionManageComponent } from './general-topic-question-manage.component';
import { UserRouteAccessService } from '../core';
import { Routes } from '@angular/router';
import { GeneralTopicQuestionCreateComponent } from './general-topic-question-create.component';
import { MCQStandardComponent } from '../question-builder/mcq-standard';
import { MatchlistComponent } from '../question-builder/matchlist/matchlist.component';
import { MCQChoiceMatrixComponent } from '../question-builder/mcq-choicematrix';
import { GeneralTopicContentManageComponent } from './general-topic-content-manage.component';
import { CurriculumTopicQuestionDetailsComponent } from '../syllabus-lesson-topic/topic-question/topic-question-details.component';
import { DragAndDropComponent } from '../question-builder/cloze/drag-n-drop/drag-n-drop.component';
import { CurriculumTopicQuestionUpdateComponent } from '../syllabus-lesson-topic/topic-question/topic-question-update.component';

export const generalConceptAndTopicRoute: Routes = [
    {
        path: "", redirectTo: "general-topic-question", pathMatch: "full"
    },
    {
        path: 'general-topic-question',
        component: GeneralTopicQuestionManageComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Manage general topic questions'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/general-topic-question-create',
        component: GeneralTopicQuestionCreateComponent,
        children: [
            {
                path: 'mcqstandard',
                component: MCQStandardComponent,
                data: {
                    type: "MCQ_SINGLE",
                    title: "Create"
                },
            },
            {
                path: "mcqstandard/:qid",
                component: MCQStandardComponent,
                data: {
                    type: "MCQ_SINGLE",
                    title: "Edit"
                }
            },
            {
                path: "mcqmultiple",
                component: MCQStandardComponent,
                data: {
                    type: "MCQ_MULTIPLE",
                    mode: "Create"
                }
            },
            {
                path: "mcqmultiple/:qid",
                component: MCQStandardComponent,
                data: {
                    type: "MCQ_MULTIPLE",
                    mode: "Edit"
                }
            },
            {
                path: "mcqtruefalse",
                component: MCQStandardComponent,
                data: {
                    type: "MCQ_TRUE_FALSE",
                    title: "Create"
                }
            },
            {
                path: "mcqtruefalse/:qid",
                component: MCQStandardComponent,
                data: {
                    type: "MCQ_TRUE_FALSE",
                    title: "Edit"
                }
            },
            {
                path: 'mcqchoicematrix',
                component: MCQChoiceMatrixComponent,
                data: {
                    title: "Create",
                    type: "MCQ_CHOICE_MATRIX"
                }
            }, {
                path: 'mcqchoicematrix/:qid',
                component: MCQChoiceMatrixComponent,
                data: {
                    title: "Edit",
                    type: "MCQ_CHOICE_MATRIX"
                }
            }, {
                path: "matchlist",
                component: MatchlistComponent,
                data: {
                    title: "Create",
                    type: "MCQ_MATCH_LIST"
                }
            },
            {
                path: "matchlist/:qid",
                component: MatchlistComponent,
                data: {
                    title: "Edit",
                    type: "MCQ_MATCH_LIST"
                }
            },
            {
                path: "clozedrag",
                component: DragAndDropComponent,
                data: {
                    title: "Create",
                    type: "mcq-drag"
                }
            },
            {
                path: "clozedrag/:qid",
                component: DragAndDropComponent,
                data: {
                    title: "Edit",
                    type: "mcq-drag"
                }
            }
        ],
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Create general topic question'
        },
        canActivate: [UserRouteAccessService]
    },
    {
        path: ':id/general-topic-question-view',
        component: CurriculumTopicQuestionDetailsComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'View general topic question'
        },
        canActivate: [UserRouteAccessService]

    },
    {
        path: ':id/general-topic-question-edit',
        component: CurriculumTopicQuestionUpdateComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Edit general topic question'
        },
        canActivate: [UserRouteAccessService]

    },
    {
        path: 'general-topic-content',
        component: GeneralTopicContentManageComponent,
        data: {
            authorities: ['ROLE_USER'],
            pageTitle: 'Manage general topic content'
        },
        canActivate: [UserRouteAccessService]
    }
];
